import React, { useEffect, useState } from 'react'
import { Button, Form, Col, FormGroup, Input, InputGroup, InputGroupAddon, Table, Row } from 'reactstrap'
import SearchBarIcon from '../Icons/HeaderIcons/SearchBarIcon'
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Widget from "../../components/Widget/Widget.js";
import successIcon from "../../assets/notificationsIcons/successIcon.svg";
import closeIcon from "../../assets/notificationsIcons/closeIcon.svg";
import { Box, Tab, Tabs, Typography } from '@material-ui/core';
import Notification from '../Notification/Notification';
import { spellcheckAPI } from '../../api/spellcheck';
import { qpsAPI } from '../../api/qps';
import { mappingsAPI } from '../../api/mappings';
import ReactJson from 'react-json-view';
import { facetsAPI } from '../../api/facets';
import displayNames from "../../data/solrFieldDisplayName.json"
import { searchAPI } from '../../api/search';

const options = {
    autoClose: 4000,
    closeButton: false,
    hideProgressBar: true,
    position: toast.POSITION.BOTTOM_RIGHT,
};

function TabPanel(props) {
    const { children, value, index } = props
    if (value !== index) {
        return null
    }

    return (
        <Box
            style={{
                flex: 1,
                display: 'flex',
                overflow: 'auto',
                width: '100%',
                height: '100%',
                flexDirection: 'column',
            }}
        >
            {children}
        </Box>
    )
}

function UniversalSearch() {
    const [searchTerm, setSearchTerm] = useState('')
    const [category, setCategory] = useState('')
    const [tab, setTab] = useState('spellcheck')
    const [response, setResponse] = useState([])
    const copyToClipboard = () => {
        toast(<Notification message="copied to clipboard" type="info" />, options)
    }

    const handleSubmit = async e => {
        e.preventDefault()
        const spellcheckData = await spellcheckAPI.getNormalizeQuery(searchTerm)
        setResponse(prev => ({ ...prev, spellcheck: spellcheckData.data }))
        const qpsData = await qpsAPI.getQpsData(spellcheckData.data.normalized_query)
        setResponse(prev => ({ ...prev, qps: qpsData.data }))
        let categories = qpsData.data.filters.find(e => e.field_name == "category_en_string_mv").values
        if (category) {
            categories = category.startsWith("[") ? JSON.parse(category) : category.toUpperCase().split(/\s*[|,]\s*/)
        }
        facetsAPI.getServiceFacets({ search_term: spellcheckData.data.normalized_query, category_id: categories.join("|") }).then(e => setResponse(prev => ({ ...prev, dynamic_facets: e.data })))
        searchAPI.getBFFResponse({ search_term: searchTerm, category_id: categories.join("|") }).then(e => setResponse(prev => ({ ...prev, bff: e.data })))
    }
    return (
        <div style={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
            <Form className="d-none d-sm-block" inline onSubmit={handleSubmit}>
                <Box display="flex"><Input style={{ flex: 1, marginRight: 10 }} onChange={e => setSearchTerm(e.target.value)} value={searchTerm} id="search-input" placeholder="Search term" className='focus' />
                    <Input style={{ flex: 1, marginRight: 10 }} onChange={e => setCategory(e.target.value)} value={category} id="search-input" placeholder="Categories" className='focus' />
                    <Button onClick={handleSubmit}>Find</Button>
                </Box>
            </Form>
            <Box flex={1} overflow="auto" height="100%">
                <Tabs style={{ zIndex: 1, background: 'white', position: 'sticky', top: 0 }} onChange={(e, v) => setTab(v)} value={tab}>
                    <Tab value="spellcheck" label="SpellCheck" />
                    <Tab value="qps" label="QPS" />
                    <Tab value="dynamic_facets" label="Dynamic Facets" />
                    <Tab value="bff" label="BFF" />
                </Tabs>
                <Box flex={1} overflow="auto">
                    <TabPanel value={tab} index="spellcheck">
                        <ReactJson quotesOnKeys={false} theme="monokai" displayDataTypes={false} src={response.spellcheck} />
                    </TabPanel>
                    <TabPanel value={tab} index="qps">
                        <ReactJson quotesOnKeys={false} collapsed={1} theme="monokai" displayDataTypes={false} src={response.qps} />
                    </TabPanel>
                    <TabPanel value={tab} index="dynamic_facets">
                        <Box flex={1} display="flex">
                            <Box flex={1} mt={2} height="100%" overflow="auto">
                                <Table responsive>
                                    <tbody>
                                        {response.dynamic_facets?.facets.map((facet, i) => <tr>
                                            <td className="w-25"><Box>{displayNames[facet.name]}</Box><Box><Typography variant='caption' style={{ paddingRight: 10 }}>{facet.name}</Typography></Box></td>
                                        </tr>)}
                                    </tbody>
                                </Table>
                            </Box>
                            <ReactJson quotesOnKeys={false} style={{ flex: 1 }} collapsed={2} theme="monokai" displayDataTypes={false} src={response.dynamic_facets} />
                        </Box>
                    </TabPanel>
                    <TabPanel value={tab} index="bff">
                        <ReactJson quotesOnKeys={false} collapsed={1} theme="monokai" displayDataTypes={false} src={response.bff} />
                    </TabPanel>
                </Box>
            </Box>
        </div >
    )
}

export default UniversalSearch